/**
 * Inline search form.
 *
 * Don't run this via patterns.js, it's a separate compilation.
 *
 * @module patterns/search/search--inline
 * @author Web Development
 * @copyright City, University of London 2020
 */

import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { find, suggest } from '../finder/funnelback';
import createFocusTrap from 'focus-trap';
import Query from './components/inline/search--inline__query';
import Suggestions from './components/inline/search--inline__suggestions';
import TopResults from './components/inline/search--inline__topresults';

let modal = null;
const getmodal = () => modal;

/**
 * Launch the inline search.
 *
 * @param {object} props The JSON configuration file for search.
 * @return {object} The React component to render.
 */
function InlineSearch(props) {
    const [display, setDisplay] = useState(false);
    const [focusTrap, setFocusTrap] = useState({});

    const maximumSuggestions = 10;

    const initialQuery = {
        collection: props.config.collection,
        misspelling: null,
        numRanks: props.config.numRanks,
        query: '',
        startRank: 1,
        facets: [],
        fixedFacets: [],
        fixedParameters: [],
    };

    /**
     * Dummy, empty Funnelback response object for initial state.
     */
    const initialResponse = Object.freeze({
        bestBets: [],
        facets: [],
        results: [],
        spell: null,
        summary: {
            currEnd: 0,
            currStart: 0,
            numRanks: 0,
            totalMatching: 0,
        },
    });

    const initialSuggestions = [];

    // State objects for the Funnelback query and response
    const [query, setQuery] = useState(initialQuery);
    const [funnelbackResponse, setResponse] = useState(initialResponse);
    const [suggestions, setSuggestions] = useState(initialSuggestions);

    // Boolean to indicate when a query is in progress
    const [updating, setUpdating] = useState(false);

    // Boolean to indicate when a query is in progress
    const [suggestionsUpdating, setSuggestionsUpdating] = useState(false);

    // Request token from the Funnelback request object, so we can cancel if
    // another request is triggered by the user
    const [call, setCall] = useState({
        cancel: () => {},
    });

    // useEffect doesn't deep inspect objects, so we need an additional, plain
    // state variable to indicate that the query state has changed and the
    // component should render
    // the value isn't important, it's just easy to toggle a bool back and forth
    const [update, setUpdate] = useState(false);

    // Request token from the Funnelback request object, so we can cancel if
    // another request is triggered by the user
    const [suggestionsCall, setSuggestionsCall] = useState({
        cancel: () => {},
    });

    // Retrieve Funnelback results
    useEffect(() => {
        // indicate a request is in progress
        setUpdating(true);
        setSuggestionsUpdating(true);

        /**
         * cancel any requests already in progress
         *
         * async requests can return out of order
         */
        call.cancel();
        suggestionsCall.cancel();

        // make new, asynchronous requests to Funnelback
        const [request, requestToken] = find(
            query.collection,
            query.fixedFacets,
            query.fixedParameters,
            query.query,
            query.sortType,
            query.startRank,
            query.numRanks,
            query.facets
        );

        const [suggestionsRequest, suggestionsRequestToken] = suggest(
            query.collection,
            query.query
        );

        // save the requestTokens
        setCall({
            cancel: () => {
                requestToken.cancel();
            },
        });

        setSuggestionsCall({
            cancel: () => {
                suggestionsRequestToken.cancel();
            },
        });

        // when the response from Funnelback arrives,
        // update the results and display them
        request
            .then((data) => {
                setResponse(data);
                setUpdating(false);
            })
            .catch(() => {
                setResponse(initialResponse);
            });

        suggestionsRequest
            .then((data) => {
                if (data) {
                    setSuggestions(data);
                    setSuggestionsUpdating(false);
                }
            })
            .catch(() => {
                setSuggestions(initialSuggestions);
            });

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [update]);

    // update props so child components can update the query
    const updater = {
        query: (newQuery) => setQuery(newQuery),
        results: (newUpdate) => setUpdate(newUpdate),
        updateState: update,
    };

    const updateIndicator = (what) => (
        <p
            className={`search--inline__results__updating search--inline__results__updating--${what.replace(
                / /g,
                ''
            )}`}
            aria-live="polite"
        >
            <span className="fas fa-spinner fa-pulse icon" aria-hidden="true" />{' '}
            <span>Updating {what}</span>
        </p>
    );

    // trap tab focus when the filters are open
    useEffect(() => {
        if (!focusTrap.activate) {
            setFocusTrap(
                createFocusTrap(getmodal(), {
                    initialFocus: getmodal().querySelector('input'),
                    onDeactivate: () => setDisplay(false),
                    clickOutsideDeactivates: true,
                })
            );
        }

        if (display) {
            focusTrap.activate && focusTrap.activate();
        } else {
            focusTrap.deactivate && focusTrap.deactivate();
        }
    }, [display, focusTrap]);

    const iconClass = `global-header__search__icon far fa-fw ${
            display ? 'fa-times' : 'fa-search'
        }`,
        veil = display && <div className="veil--search" data-on="true"></div>,
        toggleButton = (
            <button
                className="global-header__search__button"
                type="button"
                id="search"
                aria-expanded={display}
                onClick={() => {
                    setDisplay(!display);
                }}
            >
                <div className="global-header__search__wrapper">
                    <span className="global-header__search__text">
                        {display ? 'Close' : 'Search'}
                    </span>
                    <span className={iconClass} aria-hidden="true"></span>
                </div>
            </button>
        ),
        inputField = (
            <Query
                config={props.config}
                query={query}
                update={updater}
                updating={updating}
            />
        ),
        suggestionsList = suggestionsUpdating ? (
            updateIndicator('suggestions')
        ) : suggestions && suggestions.length > 0 ? (
            <Suggestions
                query={query}
                suggestions={suggestions.slice(0, maximumSuggestions)}
            />
        ) : null,
        topResults = updating ? (
            updateIndicator('top results')
        ) : funnelbackResponse && funnelbackResponse.results.length > 0 ? (
            <TopResults query={query} results={funnelbackResponse.results} />
        ) : null;

    return (
        <>
            {toggleButton}
            <div
                className="global-header__search__modal"
                ref={(modalRef) => (modal = modalRef)}
            >
                <div className="global-header__search__modal__header">
                    <div className="header-logo header-logo--city">
                        <div className="header-logo__responsive">
                            <a href="https://city.ac.uk" rel="home">
                                <img
                                    className="logo"
                                    src="https://www.city.ac.uk/__data/assets/file/0006/442248/city-university-london-responsive-white.svg"
                                    width="180"
                                    alt="City, University of London"
                                />
                            </a>
                        </div>
                    </div>
                    {toggleButton}
                </div>
                <div className="global-header__search__modal__body">
                    <h2>What are you looking for?</h2>
                    <form action="/search" method="get">
                        {inputField}
                    </form>
                    <div className="global-header__search__modal__body__results">
                        {suggestionsList}
                        {topResults}
                    </div>
                </div>
            </div>
            {veil}
        </>
    );
}

InlineSearch.propTypes = {
    config: PropTypes.object,
    element: PropTypes.object,
};

export default InlineSearch;
