import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Clear from './search--inline__clear';

const escapeKey = 'Escape';

/**
 * Search input field and autocomplete.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function InlineSearch__Query(props) {
    // save what they're typing
    const [partialQuery, setPartialQuery] = useState(props.query.query);
    // ref for the input field, so we can .focus() it
    const stringLength = 16,
        stringOffset = -4,
        [inputId] = useState(
            'search--inline--' +
                props.query.collection +
                '--' +
                Math.random().toString(stringLength).slice(stringOffset)
        );

    useEffect(() => {
        setPartialQuery(props.query.query);
    }, [props.query.query, props.updating]);

    let textInput = null;
    function focusInput() {
        textInput.focus();
    }

    // on clear, make a default request for results
    const clearQuery = () => {
        setPartialQuery('');
        focusInput();

        const newQuery = props.query;

        newQuery.query = '';
        newQuery.startRank = 1;

        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    const fetchSuggestions = (partialQuery) => {
        const newQuery = props.query;

        newQuery.query = partialQuery;
        newQuery.startRank = 1;

        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    const clear = partialQuery && (
        <Clear
            clear={() => {
                clearQuery();
            }}
        />
    );

    const input = (
        <div className="search--inline__query__input">
            <div className="search--inline__query__icon--wrapper">
                <span
                    className="search--inline__icon fal fa-search icon"
                    aria-hidden="true"
                ></span>
            </div>
            <label className="sr-only" htmlFor={inputId}>
                Search
            </label>
            <input
                autoComplete="off"
                id={inputId}
                name="query"
                placeholder={props.config.placeholder}
                ref={(input) => {
                    textInput = input;
                }}
                type="text"
                required={true}
                value={partialQuery}
                onKeyDown={(e) => {
                    switch (e.key) {
                        case escapeKey:
                            clearQuery();
                            break;
                        default:
                            break;
                    }
                }}
                onChange={(e) => {
                    setPartialQuery(e.target.value);
                    fetchSuggestions(e.target.value);
                }}
            />
            {clear}
        </div>
    );

    return (
        <fieldset className="search--inline__query">
            <div>
                {input}
                <button type="submit" className="search--inline__query__submit">
                    <span className="search--inline__query__submit__content">
                        <span
                            className="fas fa-search search--inline__query__submit__icon"
                            aria-hidden="true"
                        />{' '}
                        <span className="search--inline__query__submit__text">
                            Search
                        </span>
                    </span>
                </button>
            </div>
        </fieldset>
    );
}

InlineSearch__Query.propTypes = {
    config: PropTypes.object,
    query: PropTypes.object,
    update: PropTypes.object,
    updating: PropTypes.bool,
};

export default InlineSearch__Query;
