/**
 * @module patterns/search/components/main/results/search__results
 * @author Web Development
 * @copyright City, University of London 2020
 */

import React, { useState } from 'react';
import PropTypes from 'prop-types';
import Card from '../../../../finder/components/cards/finder__results__card';
import Pagination from '../../../../finder/components/results/finder__pagination';

// FIXME for go live
const siteUrl = 'city.ac.uk';

const endKey = 'End',
    homeKey = 'Home',
    arrowLeft = 'ArrowLeft',
    arrowUp = 'ArrowUp',
    arrowRight = 'ArrowRight',
    arrowDown = 'ArrowDown';

function genericResult(result, type, i) {
    const label = type && <p className="card__label">{type}</p>,
        external = result.liveUrl.indexOf(siteUrl) < 0 && (
            <>
                {' '}
                <span
                    className="fas fa-external-link icon"
                    aria-label="(external link)"
                ></span>
            </>
        );

    return (
        <li className="card--search" key={'all' + i}>
            <a
                href={result.clickTrackingUrl}
                className="card__details card__anchor"
            >
                <div className="card__details__text">
                    {label}
                    <h3 className="card__heading">
                        {result.title}
                        {external}
                    </h3>
                    <p className="card__description">{result.metaData.c}</p>
                    <p className="card__url">{result.liveUrl}</p>
                </div>
            </a>
        </li>
    );
}

/**
 * Render Funnelback results.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Search__Results(props) {
    const [currentTab, setCurrentTab] = useState(0);

    const updating = (results) => {
        return (
            <p className="search__results__updating" aria-live="polite">
                <span
                    className="fas fa-spinner fa-pulse icon"
                    aria-hidden="true"
                />{' '}
                <span>Updating {results}</span>
            </p>
        );
    };

    /**
     * Respond to cursor key events inside the tabbed section.
     *
     * @param {Event} e - The initiating keydown event.
     */
    function keyEvents(e) {
        switch (e.key) {
            case arrowLeft:
                props.element
                    .querySelector(
                        `.tabs__links li:nth-child(${
                            currentTab !== 0 ? currentTab : 1
                        }) button`
                    )
                    .focus();

                currentTab !== 0 && setCurrentTab(currentTab - 1);
                break;
            case arrowRight:
                props.element
                    .querySelector(
                        `.tabs__links li:nth-child(${
                            currentTab !== props.finders.length
                                ? currentTab + 2
                                : currentTab + 1
                        }) button`
                    )
                    .focus();

                currentTab !== props.finders.length &&
                    setCurrentTab(currentTab + 1);
                break;
            case arrowUp:
                /**
                 * Move focus to the currently selected tab control.
                 */
                props.element
                    .querySelector('.tabs__links button[aria-current="true"]')
                    .focus();
                break;
            case arrowDown:
                /**
                 * Move focus into the tab content.
                 */
                props.element
                    .querySelector('.tabs__panel:not([hidden])')
                    .focus();
                break;
            case homeKey:
                /**
                 * First tab
                 */
                setCurrentTab(0);
                break;
            case endKey:
                /**
                 * Last tab
                 */
                setCurrentTab(props.finders.length);
                break;
            default:
                break;
        }
    }

    const links = (
        <ul className="tabs__links" role="tablist">
            <li key="tabs__links__primary">
                <button
                    role="tab"
                    id={
                        'link--search443531__tabs__' +
                        props.primary[0].collection
                    }
                    title={props.primary[0].label}
                    aria-selected={currentTab === 0}
                    aria-current={currentTab === 0}
                    onClick={() => setCurrentTab(0)}
                >
                    <span>
                        {props.primary[0].label} (
                        {props.primary[0].updating ? (
                            <span
                                className="fas fa-spinner fa-pulse icon"
                                aria-label="Updating"
                            />
                        ) : (
                            props.primary[0].response &&
                            props.primary[0].response.summary.totalMatching
                        )}
                        )
                    </span>
                </button>
            </li>
            {props.finders.map((finder, i) => {
                return (
                    <li key={'tabs__links__finder' + i} role="presentation">
                        <button
                            id={
                                'link--search443531__tabs__' + finder.collection
                            }
                            title={finder.label}
                            aria-selected={currentTab === i + 1}
                            aria-current={currentTab === i + 1}
                            onClick={() => setCurrentTab(i + 1)}
                        >
                            <span>
                                {finder.label} (
                                {finder.updating || !finder.response ? (
                                    <span
                                        className="fas fa-spinner fa-pulse icon"
                                        aria-label="Updating"
                                    />
                                ) : (
                                    finder.response.summary.totalMatching
                                )}
                                )
                            </span>
                        </button>
                    </li>
                );
            })}
        </ul>
    );

    // if we have more results than will fit on a single page, we need
    // pagination
    const primaryPagination = props.primary[0].response &&
        props.primary[0].response.summary.totalMatching >
            props.primary[0].response.summary.numRanks && (
            <Pagination
                currStart={props.primary[0].response.summary.currStart}
                numRanks={props.primary[0].response.summary.numRanks}
                query={props.query}
                totalMatching={props.primary[0].response.summary.totalMatching}
                update={props.update}
            />
        );

    const panels = (
        <React.Fragment>
            <section
                className="tabs__panel"
                id={'search443531__tabs__' + props.primary[0].collection}
                aria-labelledby={
                    'link--search443531__tabs__' + props.primary[0].collection
                }
                key="primary"
                role="tabpanel"
                tabIndex="-1"
                hidden={currentTab !== 0}
            >
                <h2 className="tabs__panel__heading sr-only">
                    {props.primary[0].label}
                </h2>
                <div className="tabs__panel__body">
                    {props.primary[0].updating ? (
                        updating('results')
                    ) : props.primary[0].response &&
                      props.primary[0].response.summary.totalMatching > 0 ? (
                        <div className="search__results">
                            <div className="search__results__summary">
                                <h3
                                    aria-live="polite"
                                    className="search__results__summary__heading"
                                >
                                    <span>
                                        (showing{' '}
                                        <span>
                                            {
                                                props.primary[0].response
                                                    .summary.currStart
                                            }
                                        </span>
                                        
                                        <span>
                                            {
                                                props.primary[0].response
                                                    .summary.currEnd
                                            }
                                        </span>{' '}
                                        of{' '}
                                        <span>
                                            {
                                                props.primary[0].response
                                                    .summary.totalMatching
                                            }
                                        </span>{' '}
                                        {props.primary[0].response.summary
                                            .totalMatching === 0
                                            ? 'results'
                                            : props.primary[0].response.summary
                                                  .totalMatching === 1
                                            ? 'result'
                                            : 'results'}
                                        )
                                    </span>
                                </h3>
                            </div>
                            <ol
                                className="search__results__list"
                                start={
                                    props.primary[0].response.summary.currStart
                                }
                            >
                                {props.primary[0].response.results.map(
                                    (result, i) => {
                                        switch (result.collection) {
                                            case 'web2020-residentialhalls':
                                                return genericResult(
                                                    result,
                                                    'Residential hall',
                                                    i
                                                );
                                            case 'web2020-business-impact':
                                                return genericResult(
                                                    result,
                                                    'Case study',
                                                    i
                                                );
                                            case 'web2020-research':
                                                return genericResult(
                                                    result,
                                                    'Case study',
                                                    i
                                                );
                                            case 'web2020-centres':
                                                return genericResult(
                                                    result,
                                                    'Research centre',
                                                    i
                                                );
                                            case 'web2020-courses':
                                                return genericResult(
                                                    result,
                                                    'Course',
                                                    i
                                                );
                                            case 'web2020-events':
                                                return genericResult(
                                                    result,
                                                    'Event',
                                                    i
                                                );
                                            case 'web2020-facilities':
                                                return genericResult(
                                                    result,
                                                    'Facility',
                                                    i
                                                );
                                            case 'web2020-locations':
                                                return genericResult(
                                                    result,
                                                    'Location',
                                                    i
                                                );
                                            case 'web2020-pathways':
                                                return genericResult(
                                                    result,
                                                    'Career pathway',
                                                    i
                                                );
                                            case 'web2020-funding':
                                                return genericResult(
                                                    result,
                                                    'Funding',
                                                    i
                                                );
                                            case 'web2020-news':
                                                return genericResult(
                                                    result,
                                                    'News',
                                                    i
                                                );
                                            case 'web2020-profiles':
                                                return genericResult(
                                                    result,
                                                    'Profile',
                                                    i
                                                );
                                            default:
                                                return genericResult(
                                                    result,
                                                    null,
                                                    i
                                                );
                                        }
                                    }
                                )}
                            </ol>
                            {primaryPagination}
                        </div>
                    ) : (
                        <div className="search__results__summary search__results__summary--noresults">
                            <h3 className="search__results__summary__heading">
                                Your search did not match any results.
                            </h3>
                            <p>Suggestions:</p>
                            <ul>
                                <li>
                                    Make sure that all words are spelled
                                    correctly
                                </li>
                                <li>Try different keywords</li>
                                <li>Try more general keywords</li>
                                <li>Try fewer keywords</li>
                            </ul>
                        </div>
                    )}
                </div>
            </section>
            {props.finders.map((finder, i) => {
                return (
                    <section
                        className="tabs__panel"
                        id={'search443531__tabs__' + finder.collection}
                        aria-labelledby={
                            'link--search443531__tabs__' + finder.collection
                        }
                        key={'finder' + i}
                        role="tabpanel"
                        tabIndex="-1"
                        hidden={currentTab !== i + 1}
                    >
                        <h2 className="tabs__panel__heading sr-only">
                            {finder.label}
                        </h2>
                        <div className="tabs__panel__body">
                            {finder.updating || !finder.response ? (
                                updating(finder.summariseAs.plural)
                            ) : finder.response.summary.totalMatching > 0 ? (
                                <div className="search__results">
                                    <div className="search__results__summary">
                                        <h3
                                            aria-live="polite"
                                            className="search__results__summary__heading"
                                        >
                                            <span>
                                                (showing{' '}
                                                <span>
                                                    {finder.response &&
                                                        finder.response.summary
                                                            .currStart}
                                                </span>
                                                
                                                <span>
                                                    {finder.response &&
                                                        finder.response.summary
                                                            .currEnd}
                                                </span>{' '}
                                                of{' '}
                                                <span>
                                                    {finder.response &&
                                                        finder.response.summary
                                                            .totalMatching}
                                                </span>{' '}
                                                {finder.response &&
                                                finder.response.summary
                                                    .totalMatching === 0
                                                    ? finder.summariseAs.plural
                                                    : finder.response &&
                                                      finder.response.summary
                                                          .totalMatching === 1
                                                    ? finder.summariseAs
                                                          .singular
                                                    : finder.summariseAs.plural}
                                                )
                                            </span>
                                        </h3>
                                    </div>
                                    <ol
                                        className="search__results__list"
                                        start={
                                            finder.response &&
                                            finder.response.summary.currStart
                                        }
                                    >
                                        {finder.response &&
                                            finder.response.results.map(
                                                (result, i) => (
                                                    <Card
                                                        bestBet={false}
                                                        details={result}
                                                        key={
                                                            finder.collection +
                                                            i
                                                        }
                                                        type={finder.resultCard}
                                                        query={props.query}
                                                    />
                                                )
                                            )}
                                    </ol>
                                    {finder.response &&
                                        finder.response.summary.totalMatching >
                                            finder.response.summary.currEnd &&
                                        finder.search && (
                                            <p className="search__results__more">
                                                <a
                                                    href={
                                                        finder.search +
                                                        '?query=' +
                                                        props.query.query
                                                    }
                                                >
                                                    Browse more{' '}
                                                    {finder.summariseAs.plural}{' '}
                                                </a>{' '}
                                                <span
                                                    className="fas fa-arrow-right icon"
                                                    aria-hidden="true"
                                                ></span>
                                            </p>
                                        )}
                                </div>
                            ) : (
                                <p className="search__summary__noresults">
                                    No matching {finder.summariseAs.plural}.{' '}
                                    {finder.search && (
                                        <>
                                            <a href={finder.search}>
                                                Browse{' '}
                                                {finder.summariseAs.plural}{' '}
                                                instead
                                            </a>{' '}
                                            <span
                                                className="fas fa-arrow-right icon"
                                                aria-hidden="true"
                                            ></span>
                                        </>
                                    )}
                                </p>
                            )}
                        </div>
                    </section>
                );
            })}
        </React.Fragment>
    );

    return (
        <div
            className="tabs wrapper--search__results"
            onKeyDown={(e) => keyEvents(e)}
        >
            {links}
            {panels}
        </div>
    );
}

Search__Results.propTypes = {
    config: PropTypes.object,
    element: PropTypes.object,
    finders: PropTypes.arrayOf(PropTypes.object),
    primary: PropTypes.arrayOf(PropTypes.object),
    query: PropTypes.object,
    update: PropTypes.object,
};

export default Search__Results;
