import React from 'react';
import PropTypes from 'prop-types';

function highlightQueryTerm(suggestion, partialQuery) {
    return (
        <>
            <span>
                <span className="sr-only">{suggestion}</span>
                <span aria-hidden="true">
                    <span className="search--inline__suggestion">
                        {suggestion.slice(
                            suggestion.indexOf(partialQuery.toLowerCase()),
                            partialQuery.length
                        )}
                    </span>
                    {suggestion.slice(
                        suggestion.indexOf(partialQuery.toLowerCase()) +
                            partialQuery.length
                    )}
                </span>
            </span>
        </>
    );
}

/**
 * Suggestions.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function InlineSearch__Suggestions(props) {
    return (
        <div className="search--inline__suggestions">
            <h3>Suggestions</h3>
            <ul>
                {props.suggestions.map((suggestion, i) => {
                    const urlString = suggestion.replace(/\s+/g, '+'),
                        highlighted = highlightQueryTerm(
                            suggestion,
                            props.query.query
                        );

                    return (
                        <li key={i}>
                            <a href={`/search?query=${urlString}`}>
                                {highlighted}
                            </a>
                        </li>
                    );
                })}
            </ul>
        </div>
    );
}

InlineSearch__Suggestions.propTypes = {
    query: PropTypes.object,
    suggestions: PropTypes.arrayOf(PropTypes.string),
};

export default InlineSearch__Suggestions;
