import React from 'react';
import PropTypes from 'prop-types';

const maximumBestBetsToDisplay = 3;

/**
 * Render Funnelback results.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Search__BestBets(props) {
    return (
        <div className="wrapper--search__bestbets">
            <div className="search__bestbets">
                <h2 className="search__bestbets__heading sr-only">Best bets</h2>
                <ul className="search__bestbets__list">
                    {props.bestbets
                        .slice(0, maximumBestBetsToDisplay)
                        .map((bestBet, i) => (
                            <li
                                className="card--search card--bestbet"
                                key={'bestBet' + i}
                            >
                                <a
                                    href={bestBet.linkUrl}
                                    className="card__anchor card__details"
                                >
                                    <div className="card__details__text">
                                        <h3 className="card__heading">
                                            {bestBet.titleHtml}
                                        </h3>
                                        <p className="card__description">
                                            {bestBet.descriptionHtml}
                                        </p>
                                        <p className="card__url">
                                            {bestBet.displayUrl}
                                        </p>
                                    </div>
                                </a>
                            </li>
                        ))}
                </ul>
            </div>
        </div>
    );
}

Search__BestBets.propTypes = {
    bestbets: PropTypes.arrayOf(PropTypes.object),
};

export default Search__BestBets;
