import React from 'react';
import PropTypes from 'prop-types';
// import { formatReactDate } from '../../../../util';

function formatShortDate(dateString) {
    const months = [
            'Jan',
            'Feb',
            'Mar',
            'Apr',
            'May',
            'Jun',
            'Jul',
            'Aug',
            'Sep',
            'Oct',
            'Nov',
            'Dec',
        ],
        date = new Date(dateString),
        month = months[date.getUTCMonth()];

    return (
        <p className="card--event__label--shortdate">
            {month} <span>{date.getUTCDate()}</span>
        </p>
    );
}

/**
 * Render a Funnelback result as an event card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Results__Event(props) {
    const shortDate =
            props.details.metaData.d &&
            formatShortDate(props.details.metaData.d.split('|')[0]),
        location = props.details.metaData.location && (
            <p className="card__tag">
                <span
                    className="fas fa-fw icon fa-map-marker-alt"
                    aria-hidden="true"
                ></span>{' '}
                <span>Location:</span>{' '}
                <span>
                    {props.details.metaData.location.split('|').join(', ')}
                </span>
            </p>
        ),
        audience = props.details.metaData.audience && (
            <p className="card__tag">
                <span
                    className="fas fa-fw icon fa-users"
                    aria-hidden="true"
                ></span>{' '}
                <span>Audience:</span>{' '}
                <span>{props.details.metaData.audience}</span>
            </p>
        ),
        thumbnail = props.details.metaData.thumbnail && (
            <img
                src={props.details.metaData.thumbnail}
                alt=""
                className="card__thumbnail"
            />
        );

    return (
        <li className="card card--event card--landscape">
            <a
                href={props.details.clickTrackingUrl}
                className="card__anchor card__details"
            >
                {thumbnail}
                {shortDate}
                <div className="card__details__text">
                    <h3 className="card__heading">{props.details.title}</h3>
                    <p className="card__description">
                        {props.details.metaData.c && props.details.metaData.c}
                    </p>
                    {location}
                    {audience}
                </div>
            </a>
        </li>
    );
}

Finder__Results__Event.propTypes = {
    details: PropTypes.object,
};

export default Finder__Results__Event;
