/**
 * Search form.
 *
 * Don't run this via patterns.js, it's a separate compilation.
 *
 * @module patterns/search/search
 * @author Web Development
 * @copyright City, University of London 2020
 */

import { find } from '../finder/funnelback';
import { reduceMotion } from '../../util';
import BestBets from './components/main/results/search__bestbets';
import OtherSites from './components/main/results/search__othersites';
import PropTypes from 'prop-types';
import Query from './components/main/query/search__query';
import React, { useEffect, useState } from 'react';
import scroll from 'zenscroll';
import TabResults from './components/main/results/search__results';

const oneSecond = 1000,
    scrollDuration = reduceMotion() ? 0 : oneSecond,
    screenOffsetRatio = 10;

/**
 * Preserve the search state in the URL parameters.
 *
 * @param {string} currQuery The search query.
 * @param {integer} currStartRank The start rank.
 * @param {object[]} currFacets A map of facet meta labels to their values.
 * @param {*} facetLabels Array of facet definitions.
 */
function replaceHistory(
    currQuery,
    currStartRank,
    currCollection,
    defaultCollection
) {
    if (window) {
        const params = new URLSearchParams(window.location.search);

        currQuery !== ''
            ? params.set('query', currQuery)
            : params.delete('query');

        currStartRank !== 1
            ? params.set('start_rank', currStartRank)
            : params.delete('start_rank');

        currCollection !== defaultCollection
            ? params.set('collection', currCollection)
            : params.delete('collection');

        const hasParams = params.toString().length ? '?' : '';

        window.history.replaceState(
            {},
            '',
            `${window.location.pathname}${hasParams}${params.toString()}`
        );
    }
}

/**
 * Launch search.
 *
 * @param {object} props The JSON configuration file for search.
 * @return {object} The React component to render.
 */
function Search(props) {
    const params = new URLSearchParams(window.location.search);

    /**
     * initial state for the Funnelback query, taken from URL parameters and
     * configuration
     **/
    const initialQuery = {
        collection:
            params.get('collection') ||
            props.config.primary.collections[0].collection,
        interacted: false,
        misspelling: null,
        numRanks: params.get('num_ranks') || props.config.primary.numRanks,
        query: params.get('query') || '',
        sortType: '',
        startRank: params.get('start_rank') || 1,
    };

    /**
     * Dummy, empty Funnelback response object for initial state.
     */
    const initialResponse = Object.freeze({
        bestBets: [],
        facets: [],
        results: [],
        spell: null,
        summary: {
            currEnd: 0,
            currStart: 0,
            numRanks: 0,
            totalMatching: 0,
        },
    });

    const initialResponses = {
        primary: props.config.primary.collections.map((collection) => {
            return {
                label: collection.label,
                collection: collection.collection,
                placeholder: collection.placeholder,
                response: initialResponse,
                search: collection.search,
                updating: false,
                call: {
                    cancel: () => {},
                },
            };
        }),
        finders: props.config.finders.map((finder) => {
            return {
                label: finder.label,
                summariseAs: finder.summariseAs,
                resultCard: finder.resultCard,
                search: finder.search,
                collection: finder.collection,
                response: initialResponse,
                updating: false,
                call: {
                    cancel: () => {},
                },
            };
        }),
        nonpublic: props.config.nonpublic.map((collection) => {
            return {
                label: collection.label,
                collection: collection.collection,
                response: initialResponse,
                search: collection.search,
                updating: false,
                call: {
                    cancel: () => {},
                },
            };
        }),
    };

    // eslint-disable-next-line no-unused-vars
    const [response, setResponse] = useState(0);

    // State objects for the Funnelback query and response
    const [query, setQuery] = useState(initialQuery);
    const [fbResponses, setFbResponses] = useState(initialResponses);

    // useEffect doesn't deep inspect objects, so we need an additional, plain
    // state variable to indicate that the query state has changed and the
    // component should render
    // the value isn't important, it's just easy to toggle a bool back and forth
    const [update, setUpdate] = useState(false);

    // Retrieve Funnelback results
    useEffect(() => {
        // preserve the state
        replaceHistory(
            query.query,
            query.startRank,
            query.collection,
            props.config.primary.collections[0].collection
        );

        if (query.query.length > 0) {
            query.interacted &&
                props.element.querySelector('.search__results') &&
                scroll.center(
                    props.element.querySelector('.search__results'),
                    scrollDuration,
                    -window.innerHeight / screenOffsetRatio
                );

            props.config.primary.collections.forEach((collection, i) => {
                fbResponses.primary[i].call.cancel();

                const [request, requestToken] = find(
                        collection.collection,
                        [],
                        [],
                        query.query,
                        '',
                        query.startRank,
                        query.numRanks,
                        []
                    ),
                    newResponses = fbResponses;

                newResponses.primary[i].call = {
                    cancel: () => {
                        requestToken.cancel();
                    },
                };
                newResponses.primary[i].updating = true;

                setFbResponses(newResponses);
                setResponse(Math.random());

                request
                    .then((data) => {
                        const responses = fbResponses;

                        responses.primary[i].response = data;
                        responses.primary[i].updating = false;

                        if (
                            newResponses.primary[i].collection ===
                                query.collection &&
                            data.spell &&
                            data.summary.totalMatching === 0
                        ) {
                            const newQueryText = data.spell.text
                                    .split(/\|/)[0]
                                    .trim(),
                                newQuery = query;

                            newQuery.misspelling = query.query;
                            newQuery.query = newQueryText;
                            newQuery.startRank = 1;
                            setQuery(newQuery);
                            setUpdate(!update);
                        }

                        setFbResponses(responses);
                        setResponse(Math.random());
                    })
                    .catch(() => {
                        const responses = fbResponses;

                        responses.primary[i].response = initialResponse;
                        responses.primary[i].updating = false;

                        setFbResponses(responses);
                        setResponse(Math.random());
                    });
            });

            props.config.finders.forEach((finder, i) => {
                fbResponses.finders[i].call.cancel();

                const [request, requestToken] = find(
                        finder.collection,
                        [],
                        [],
                        query.query,
                        '',
                        1,
                        finder.numRanks,
                        []
                    ),
                    newResponses = fbResponses;

                newResponses.finders[i].call = {
                    cancel: () => {
                        requestToken.cancel();
                    },
                };
                newResponses.finders[i].updating = true;

                setFbResponses(newResponses);
                setResponse(Math.random());

                request
                    .then((data) => {
                        const responses = fbResponses;

                        responses.finders[i].response = data;
                        responses.finders[i].updating = false;

                        setFbResponses(responses);
                        setResponse(Math.random());
                    })
                    .then(() => {
                        props.element.querySelector('.tabs__links') &&
                            query.interacted &&
                            scroll.center(
                                props.element.querySelector('.tabs__links'),
                                scrollDuration
                            );
                    })
                    .catch(() => {
                        const responses = fbResponses;

                        responses.finders[i].response = initialResponse;
                        responses.finders[i].updating = false;

                        setFbResponses(responses);
                        setResponse(Math.random());
                    });
            });

            props.config.nonpublic.forEach((nonpublic, i) => {
                fbResponses.nonpublic[i].call.cancel();

                const [request, requestToken] = find(
                        nonpublic.collection,
                        [],
                        [],
                        query.query,
                        '',
                        1,
                        0,
                        []
                    ),
                    newResponses = fbResponses;

                newResponses.nonpublic[i].call = {
                    cancel: () => {
                        requestToken.cancel();
                    },
                };
                newResponses.nonpublic[i].updating = true;

                setFbResponses(newResponses);
                setResponse(Math.random());

                request
                    .then((data) => {
                        const responses = fbResponses;

                        responses.nonpublic[i].response = data;
                        responses.nonpublic[i].updating = false;

                        setFbResponses(responses);
                        setResponse(Math.random());
                    })
                    .catch(() => {
                        const responses = fbResponses;

                        responses.nonpublic[i].response = initialResponse;
                        responses.nonpublic[i].updating = false;

                        setFbResponses(responses);
                        setResponse(Math.random());
                    });
            });
        } else {
            setFbResponses(initialResponses);
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [update]);

    // update props so child components can update the query
    const updater = {
        query: (newQuery) => setQuery(newQuery),
        results: (newUpdate) => setUpdate(newUpdate),
        updateState: update,
    };

    const queryElement = (
        <Query
            config={props.config}
            query={query}
            update={updater}
            primary={fbResponses.primary.filter(
                (collection) => collection.collection === query.collection
            )}
            updating={fbResponses.primary[0].updating}
        />
    );

    const otherSites = query.query && !fbResponses.primary[0].updating && (
        <OtherSites
            query={query}
            collections={fbResponses.primary.filter(
                (collection) => collection.collection !== query.collection
            )}
            nonpublic={fbResponses.nonpublic}
        />
    );

    const bestBets = query.query &&
        !fbResponses.primary[0].updating &&
        fbResponses.primary[0].response &&
        fbResponses.primary[0].response.bestBets && (
            <BestBets bestbets={fbResponses.primary[0].response.bestBets} />
        );

    const results = query.query && (
        <TabResults
            config={props.config}
            query={query}
            update={updater}
            primary={fbResponses.primary.filter(
                (collection) => collection.collection === query.collection
            )}
            finders={fbResponses.finders}
            element={props.element}
        />
    );

    return (
        <form
            className="search-form"
            onSubmit={(e) => {
                e.preventDefault();
            }}
        >
            {queryElement}
            {bestBets}
            {results}
            {otherSites}
        </form>
    );
}

Search.propTypes = {
    config: PropTypes.object,
    element: PropTypes.object,
};

export default Search;
