import React from 'react';
import PropTypes from 'prop-types';

/**
 * Render a Funnelback result as a generic card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Results__Generic(props) {
    const school = props.details.metaData.school && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-university icon"
                    aria-hidden="true"
                />{' '}
                <span>School:</span>{' '}
                <span>
                    {props.details.metaData.school.split('|').length > 2
                        ? props.details.metaData.school
                              .split('|')
                              .slice(0, -1)
                              .join(', ') +
                          ', and ' +
                          props.details.metaData.school.split('|').slice(-1)
                        : props.details.metaData.school
                              .split('|')
                              .join(', and ')}
                </span>
            </p>
        ),
        department = props.details.metaData.department && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-building icon"
                    aria-hidden="true"
                />{' '}
                <span>Department:</span>{' '}
                <span>
                    {props.details.metaData.department.split('|').length > 2
                        ? props.details.metaData.department
                              .split('|')
                              .slice(0, -1)
                              .join(', ') +
                          ', and ' +
                          props.details.metaData.department.split('|').slice(-1)
                        : props.details.metaData.department
                              .split('|')
                              .join(', and ')}
                </span>
            </p>
        );

    return (
        <li className="card card--generic">
            <a
                href={props.details.clickTrackingUrl}
                className="card__anchor card__details"
            >
                <div className="card__details__text">
                    <h3 className="card__heading">{props.details.title}</h3>
                    <p className="card__description">
                        {props.details.metaData.c && props.details.metaData.c}
                    </p>
                    {school}
                    {department}
                </div>
            </a>
        </li>
    );
}

Finder__Results__Generic.propTypes = {
    details: PropTypes.object,
};

export default Finder__Results__Generic;
