/**
 * @module patterns/finder/components/cards/finder__results__research
 * @author Web Development
 * @copyright City, University of London 2021
 */

import PropTypes from 'prop-types';
import React from 'react';
import parse from 'html-react-parser';

/**
 * Render a Funnelback result as a PhD research project card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Results__Research(props) {
    const school = props.details.metaData.school && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-university icon"
                    aria-hidden="true"
                />{' '}
                <span>School:</span>{' '}
                <span>
                    {props.details.metaData.school.split('|').length > 2
                        ? props.details.metaData.school
                              .split('|')
                              .slice(0, -1)
                              .join(', ') +
                          ' and ' +
                          props.details.metaData.school.split('|').slice(-1)
                        : props.details.metaData.school
                              .split('|')
                              .join(' and ')}
                </span>
            </p>
        ),
        department = props.details.metaData.department && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-building icon"
                    aria-hidden="true"
                />{' '}
                <span>Department:</span>{' '}
                <span>
                    {props.details.metaData.department.split('|').length > 2
                        ? props.details.metaData.department
                              .split('|')
                              .slice(0, -1)
                              .join(', ') +
                          ' and ' +
                          props.details.metaData.department.split('|').slice(-1)
                        : props.details.metaData.department
                              .split('|')
                              .join(' and ')}
                </span>
            </p>
        ),
        centre = props.details.metaData.researchcentre && (
            <p className="card__tag">
                <span className="fas fa-fw fa-vial icon" aria-hidden="true" />{' '}
                {props.details.metaData.researchcentre.split('|').length > 1 ? (
                    <span>Reseearch centres: </span>
                ) : (
                    <span>Reseearch centre: </span>
                )}
                <span>
                    {props.details.metaData.researchcentre.split('|').length > 2
                        ? parse(props.details.metaData.researchcentre)
                              .split('|')
                              .slice(0, -1)
                              .join(', ') +
                          ' and ' +
                          parse(
                              props.details.metaData.researchcentre
                                  .split('|')
                                  .slice(-1)
                          )
                        : parse(
                              props.details.metaData.researchcentre
                                  .split('|')
                                  .join(' and ')
                          )}
                </span>
            </p>
        ),
        academic = props.details.metaData.academic && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-chalkboard-teacher icon"
                    aria-hidden="true"
                />{' '}
                {props.details.metaData.academic.split('|').length > 1 ? (
                    <span>Academics: </span>
                ) : (
                    <span>Academic: </span>
                )}
                <span>
                    {props.details.metaData.academic.split('|').length > 2
                        ? parse(
                              props.details.metaData.academic
                                  .split('|')
                                  .slice(0, -1)
                                  .join(', ') +
                                  ' and ' +
                                  props.details.metaData.academic
                                      .split('|')
                                      .slice(-1)
                          )
                        : parse(
                              props.details.metaData.academic
                                  .split('|')
                                  .join(' and ')
                          )}
                </span>
            </p>
        ),
        student = props.details.metaData.student && (
            <p className="card__tag">
                <span className="fas fa-fw fa-users icon" aria-hidden="true" />{' '}
                {props.details.metaData.student.split('|').length > 1 ? (
                    <span>Students: </span>
                ) : (
                    <span>Student: </span>
                )}
                <span>
                    {props.details.metaData.student.split('|').length > 2
                        ? parse(
                              props.details.metaData.student
                                  .split('|')
                                  .slice(0, -1)
                                  .join(', ') +
                                  ' and ' +
                                  props.details.metaData.student
                                      .split('|')
                                      .slice(-1)
                          )
                        : parse(
                              props.details.metaData.student
                                  .split('|')
                                  .join(' and ')
                          )}
                </span>
            </p>
        ),
        degree = props.details.metaData.relatedDegree && (
            <p className="card__tag">
                <span
                    className="far fa-fw fa-diploma icon"
                    aria-hidden="true"
                />{' '}
                {props.details.metaData.relatedDegree.split('|').length > 1 ? (
                    <span>Related degrees: </span>
                ) : (
                    <span>Related degree: </span>
                )}
                <span>
                    {props.details.metaData.relatedDegree.split('|').length > 2
                        ? parse(
                              props.details.metaData.relatedDegree
                                  .split('|')
                                  .slice(0, -1)
                                  .join(', ') +
                                  ' and ' +
                                  props.details.metaData.relatedDegree
                                      .split('|')
                                      .slice(-1)
                          )
                        : parse(
                              props.details.metaData.relatedDegree
                                  .split('|')
                                  .join(' and ')
                          )}
                </span>
            </p>
        ),
        status = props.details.metaData.status && (
            <p className="card__tag">
                <span
                    className="far fa-fw fa-hourglass icon"
                    aria-hidden="true"
                />{' '}
                <span>Status:</span>{' '}
                <span>{props.details.metaData.status} </span>
                {props.details.metaData.status === 'Completed project' ? (
                    <span
                        className="far fa-fw fa-check-circle icon"
                        aria-hidden="true"
                    />
                ) : null}
            </p>
        );

    return (
        <li className="card card--research">
            <div className="card__details">
                <div className="card__details__text">
                    <h3 className="card--research__title-link">
                        {props.details.title}
                    </h3>
                    <p className="card__description">
                        {props.details.metaData.c && props.details.metaData.c}
                    </p>
                    {school}
                    {department}
                    {centre}
                    {degree}
                    {academic}
                    {student}
                    {status}
                </div>
            </div>
        </li>
    );
}

Finder__Results__Research.propTypes = {
    details: PropTypes.object,
};

export default Finder__Results__Research;
