import React from 'react';
import PropTypes from 'prop-types';

// FIXME for go live
const siteUrl = 'city.ac.uk';

const maximumResultsToDisplay = 0;

function genericResult(result, collection, i) {
    const thumbnail = result.metaData.thumbnail && (
            <div className="card__thumbnail">
                <picture>
                    <img src={result.metaData.thumbnail} alt="" />
                </picture>
            </div>
        ),
        external = result.liveUrl.indexOf(siteUrl) < 0 && (
            <>
                {' '}
                <span
                    className="fas fa-external-link icon"
                    aria-label="(external link)"
                ></span>
            </>
        );

    return (
        <li className="card--search" key={collection + i}>
            <a
                href={result.clickTrackingUrl}
                className="card__details card__anchor"
            >
                {thumbnail}
                <div className="card__details__text">
                    <h4 className="card__heading">
                        {result.title}
                        {external}
                    </h4>
                    <p className="card__description">{result.metaData.c}</p>
                </div>
            </a>
        </li>
    );
}

/**
 * Render Funnelback results.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Search__OtherSites(props) {
    return (
        <div className="wrapper--search__othersites">
            <div className="search__othersites">
                <h2 className="search__othersites__heading">
                    Results on other City sites
                </h2>
                {props.collections.map((collection, i) => {
                    if (
                        !collection.updating &&
                        collection.response &&
                        collection.response.summary.totalMatching > 0
                    ) {
                        return (
                            <div
                                className="search__othersites__site"
                                key={'site' + i}
                            >
                                <h3 className="search__othersites__site__heading">
                                    <a
                                        href={
                                            collection.search +
                                            '?query=' +
                                            props.query.query
                                        }
                                    >
                                        {collection.label} (
                                        {
                                            collection.response.summary
                                                .totalMatching
                                        }
                                        )
                                    </a>{' '}
                                    <span
                                        className="fas fa-arrow-right icon"
                                        aria-hidden="true"
                                    ></span>
                                </h3>
                                <div className="search__results">
                                    <ul className="search__results__list">
                                        {collection.response.results
                                            .slice(0, maximumResultsToDisplay)
                                            .map((result, j) =>
                                                genericResult(
                                                    result,
                                                    collection.collection,
                                                    j
                                                )
                                            )}
                                    </ul>
                                </div>
                            </div>
                        );
                    } else {
                        return null;
                    }
                })}
                {props.nonpublic.map((collection, i) => {
                    if (
                        !collection.updating &&
                        collection.response &&
                        collection.response.summary.totalMatching > 0
                    ) {
                        return (
                            <div
                                className="search__othersites__site"
                                key={'site' + i}
                            >
                                <h3 className="search__othersites__site__heading">
                                    <a
                                        href={
                                            collection.search +
                                            '?query=' +
                                            props.query.query
                                        }
                                    >
                                        {collection.label} (
                                        {
                                            collection.response.summary
                                                .totalMatching
                                        }
                                        )
                                    </a>{' '}
                                    <span
                                        className="fad fa-lock-alt icon"
                                        aria-label="Login required"
                                    ></span>
                                </h3>
                            </div>
                        );
                    } else {
                        return null;
                    }
                })}
                <div className="search__othersites__noresults">
                    <p>No results found.</p>
                </div>
            </div>
        </div>
    );
}

Search__OtherSites.propTypes = {
    query: PropTypes.object,
    collections: PropTypes.arrayOf(PropTypes.object),
    nonpublic: PropTypes.arrayOf(PropTypes.object),
};

export default Search__OtherSites;
