import React from 'react';
import PropTypes from 'prop-types';

/**
 * Clear input button.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function InlineSearch__Clear(props) {
    return (
        <button
            className="search--inline__clear"
            type="button"
            onClick={() => {
                props.clear();
            }}
        >
            <span className="fad fa-fw fa-times-circle icon " />
            <span className="search--inline__clear__text">Clear query</span>
        </button>
    );
}

InlineSearch__Clear.propTypes = {
    clear: PropTypes.func,
};

export default InlineSearch__Clear;
