/**
 * @module patterns/finder/components/cards/finder__results__course
 * @author Web Development
 * @copyright City, University of London 2019
 */

import PropTypes from 'prop-types';
import React from 'react';
import formatLabel from '../results/formatLabel';
import { uppercaseFirstLetterLowercaseRest } from '../../../../util';

/**
 * Render a Funnelback result as a course card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function Finder__Results__Course(props) {
    const subtitle =
            props.details.metaData.type || props.details.metaData.level || null,
        award = props.details.metaData.qualification && (
            <p className="card__tag">
                <span className="fas fa-fw fa-award icon" aria-hidden="true" />{' '}
                <span>
                    Award
                    {props.details.metaData.qualification.split('|').length >
                        1 && 's'}
                    :
                </span>{' '}
                <span>
                    {props.details.metaData.qualification.split('|').join(', ')}
                </span>
            </p>
        ),
        duration = props.details.metaData.duration && (
            <p className="card__tag">
                <span className="fal fa-fw fa-clock icon" aria-hidden="true" />{' '}
                <span>Duration:</span>{' '}
                <span>{props.details.metaData.duration}</span>
            </p>
        ),
        method = props.details.metaData.method &&
            props.details.metaData.level !==
                'Short courses and professional development' &&
            ((props.query.facets && props.query.facets.method) ||
                props.query.query.indexOf(props.details.metaData.method) >= 0 ||
                props.details.metaData.method.indexOf('Online') >= 0) && (
                <p className="card__tag">
                    <span
                        className="fas fa-fw fa-book-reader icon"
                        aria-hidden="true"
                    />{' '}
                    <span>
                        Method
                        {props.details.metaData.method.split('|').length > 1 &&
                            's'}{' '}
                        of study:
                    </span>{' '}
                    <span>
                        {uppercaseFirstLetterLowercaseRest(
                            props.details.metaData.method
                        )
                            .split('|')
                            .join(', ')}
                    </span>
                </p>
            ),
        location = props.details.metaData.location &&
            props.query.facets &&
            props.query.facets.location && (
                <p className="card__tag">
                    <span
                        className="fas fa-fw fa-map-marker-alt icon"
                        aria-hidden="true"
                    />{' '}
                    <span>Location:</span>{' '}
                    <span>
                        {/* Avoid duplication of 'Online' location label if location and method of study are both online */}
                        {props.details.metaData.location === 'Online|Online'
                            ? 'Online'
                            : props.details.metaData.location
                                  .split('|')
                                  .join(', ')}
                    </span>
                </p>
            ),
        school = props.details.metaData.school && (
            <p className="card__tag">
                <span
                    className="fas fa-fw fa-university icon"
                    aria-hidden="true"
                />{' '}
                <span>School:</span>{' '}
                <span>{props.details.metaData.school}</span>
            </p>
        ),
        // City and Business School use different metadata values; check for both
        clearing = ['Yes', 'yes'].includes(props.details.metaData.clearing) && (
            <div className="card__label card--course__label--clearing">
                <p className="card__label card--course__label--clearing">
                    Clearing
                </p>
            </div>
        ),
        external = props.details.indexUrl.indexOf('cass.city.ac.uk') >= 0 && (
            <>
                {' '}
                <span
                    className="fas fa-external-link icon"
                    aria-label="(external link)"
                ></span>
            </>
        ),
        courseCode =
            props.details.metaData.code &&
            ([
                'Undergraduate degree',
                'Foundation course',
                'Postgraduate taught degree',
            ].some((s) => props.details.metaData.level.indexOf(s) >= 0) ||
                props.query.query.indexOf(props.details.metaData.code) >= 0) ? (
                <p className="card__tag">
                    <span
                        className="fas fa-fw fa-graduation-cap icon"
                        aria-hidden="true"
                    />{' '}
                    <span>
                        Course code
                        {props.details.metaData.code.split('|').length > 1 &&
                            's'}
                        :
                    </span>{' '}
                    <span>
                        {props.details.metaData.code.split('|').join('/')}
                    </span>
                </p>
            ) : null;

    return (
        <li className="card card--course">
            <a
                href={props.details.clickTrackingUrl}
                className="card__anchor card__details"
            >
                <div className="wrapper--card__label__details">
                    {clearing}
                    <div className="card__details__text">
                        <h3 className="card__heading">
                            {props.query.query
                                ? formatLabel(
                                      props.details.title,
                                      props.query.query
                                  )
                                : props.details.title}
                            {external}
                        </h3>
                        <p className="card__type">
                            {props.query.query
                                ? formatLabel(subtitle, props.query.query)
                                : subtitle && subtitle}
                        </p>
                        <p className="card__description">
                            {props.query.query
                                ? formatLabel(
                                      props.details.metaData.c,
                                      props.query.query
                                  )
                                : props.details.metaData.c &&
                                  props.details.metaData.c}
                        </p>
                        {school}
                        {award}
                        {duration}
                        {courseCode}
                        {method}
                        {location}
                    </div>
                </div>
            </a>
        </li>
    );
}

Finder__Results__Course.propTypes = {
    details: PropTypes.object,
    query: PropTypes.object,
};

export default Finder__Results__Course;
